<template>
  <div id="app">
    <Navigation :isLogged="isLogged" :questSelected="questSelected" :quizSelected="quizSelected"/>
    <Login v-if="!isLogged" v-on:logged="logged"/>
    <QuestEdit v-if="questSelected" :token="token"/>
  </div>
</template>

<script>
import Navigation from './components/Navigation'
import QuestEdit from './components/QuestEdit'
import Login from './components/Login'

export default {
  name: 'app',
  data: function(){
    return {
      isLogged: false,
      questSelected: false,
      quizSelected: false,
      token: undefined
    };
  },
  components: {
    Navigation,
    QuestEdit,
    Login
    // QuizEdit
  },
  methods: {
    logged: function(token) {
      this.token = token;
      this.isLogged = true;
      this.toggleQuest();
    },
    toggleQuest: function() {
      this.questSelected = true;
      this.quizSelected = false;
    }
  }
}
</script>

<style>
#app {
  margin: 0;
  width: 100%;
  height: 100%;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
</style>



