<template>
<div id="questEdit">
  <h1 v-if="!questList">Загрузка...</h1>
  <div v-else class="container-fluid">
    <div class="row">
      <div class="col-2">
        <ItemList :list="questList" v-on:select="select"/>
      </div>
      <div class="col">
        <Properties v-if="selected" :data="selected"/>
      </div>
    </div>
  </div>
</div>
</template>

<script>
import ItemList from './ItemList';
import Properties from './Properties';

import axios from 'axios';


export default {
  name: 'questEdit',
  props: ['token'],
  components: {
    ItemList,
    Properties
  },
  methods: {
    // change: function(obj){
    //   this.selected = {
    //     ...this.selected,
    //     ...obj
    //   }
    //   console.log(this.selected);
    // },
    select: function(id){
      this.questList = this.questList.map((el)=>{
        if(el._id == id){
          this.selected = el;
          console.log(el);
          return {...el, selected: true};
        }
        else return {...el, selected: false};
      });
    },
    getData: function(){
      axios.post('http://localhost:4000/api/quest/list', {token: this.token}, {headers: {'Accept': 'application/json'}})
      .then((data)=>{
        console.log(data.data);
        if(data.data.success)
        this.questList = data.data.quests;
      });
    }
  },
  data() {
    return {
      questList: undefined,
      selected: undefined
    }
  },
  created: function(){
    this.getData();
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.navigation {
  /*width: 100%;*/
  margin: 10px;
}
</style>



