<template>
<div id="properties">
  <Property v-for="key in keys" :pkey="key" :value="data[key]"/>
</div>
</template>

<script>
import Property from './Property';

export default {
  name: 'properties',
  props: ['data'],
  components: {
    Property
  },
  methods: {

  },
  computed: {
    keys: function(){
      let keys = Object.keys(this.data);
      keys = keys.filter((el)=>{
        if(el=='selected') return false;
        else return true;
      });
      return keys;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#properties {
  border-left: solid 1px #0275d8;
}
</style>



