<template>
<div class="property">
  <div class="row">
    <div class="col-1">
      <p class="key bg-primary text-white">
        {{pkey}}
      </p>
    </div>
    <div class="col">
      <Properties v-if="isObj" :data="value"/>
      <TextInput v-else :value="value"/>
      <!-- <textarea v-else v-model="value" class="value form-control" rows="1"/> -->
    </div>
  </div>
</div>
</template>

<script>
import Properties from './Properties';
import TextInput from './TextInput';

export default {
  name: 'property',
  props: ['pkey', 'value'],
  components: {
    Properties,
    TextInput
  },


  computed: {
    isObj: function(){
      if(typeof this.value == 'string'){
        return false;
      } else if(typeof this.value == 'object'){
        return true;
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.property {
  margin-top: 0.5em;
}
.key {
  /*text-align: right;*/
  padding: 5px;
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.value {
  /*text-align: left;*/
}
</style>



